@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
}

body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #252525;
    color: white;
    /*border: 1px dashed white;*/
    max-width: 1100px;
    max-height: 1200px;
    margin:0 auto;
    /*margin-top: 150px;*/
}

a {
    color: lightgray;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{padding:0px;text-align:left}.won-card{background-color:#4A4A4A;border-radius:5px}.lost-card{background-color:#869080;border-radius:5px}

.MatchSmallCard{padding:20px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:20px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-city,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .innings-details{text-align:right}.MatchDetailCard .innings-details>p{margin-bottom:20px}

.TeamPage{max-height:883px;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 2;justify-self:start;align-self:end}.TeamPage .color-symbol{justify-self:center;padding:70px}.TeamPage .win-loss-section{justify-self:center;padding:60px;text-align:center}.TeamPage .team-name{font-size:3rem;margin-bottom:20px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}.TeamPage .color-symbol{margin-top:61px}.TeamPage .won-div{border:20px solid #4A4A4A;margin-bottom:5px}.TeamPage .loss-div{border:20px solid #869080;margin-bottom:5px}

.MatchPage{display:-webkit-flex;display:flex;grid-template-columns:1fr 5fr;grid-column-gap:10px;margin-top:30px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto;text-align:center}.MatchPage .color{justify-self:start;padding-top:80px;margin-left:20px}.MatchPage .won-div{border:20px solid #4A4A4A;margin-bottom:5px}.MatchPage .loss-div{border:20px solid #869080;margin-bottom:5px}.MatchPage .heading_match{text-align:center}

.yearSelector{text-align:center;list-style-type:none}.yearSelector li{margin-bottom:10px;margin-top:10px}

.TeamTile{height:170px;text-align:center;border:1px solid white;padding:40px;background-color:#4A4A4A}

.HomePage .app-name{font-size:3rem;margin:30px auto;text-align:center}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.footer{height:3rem;background:#222;place-items:center;padding:10px}.text{text-align:center}

